/* INT WIKIDOT STRUCTURE STYLING
   ============================= */

/* Styling specific to Int Branches' Wikidot Structure

/* ===DIRECTORY===
	1.  =CZECH BRANCH
	2.  =FRENCH BRANCH
	3.  =GERMAN BRANCH
	4.  =GREEK BRANCH
	5.  =ITALIAN BRANCH
	6.  =JAPANESE BRANCH
	7.  =KOREAN BRANCH
	8.  =POLISH BRANCH
	9.  =PORTUGESE BRANCH
	10. =RUSSIAN BRANCH
	11. =SIMPLIFIED CHINESE BRANCH
	12. =SPANISH BRANCH
	13. =THAI BRANCH
	14. =TRADITIONAL CHINESE BRANCH
	15. =UKRAINIAN BRANCH
	16. =VIETNAMESE BRANCH
*/

/* ===CZECH BRANCH=== */

:where(:lang(cs)) {
	:root& {
		--text-editor-desc: "Krtk popis zmn:";
		--text-pagehistory-flags: "zmny";
		--text-pagehistory-actions: "akce";
		--text-pagehistory-by: "od";
		--text-pagehistory-date: "datum";
		--text-sitetools: "Nstroje strnky";
	}

	#page-options-container {
		& div[id*="page-options-bottom"] {
			&:not(.page-rate-widget-box, #search-top-box-form) {
				> a.btn {
					--button-padding: calc(var(--icon-size) / 4) calc(var(--icon-size) / 8);

					height: auto;
					line-height: 1;
					white-space: normal;
				}
			}
		}
	}

	& div#page-options-bottom-2 {
		&:not(.page-rate-widget-box, #search-top-box-form) {
			> a.btn {
				flex-basis: 6rem;
			}
		}
	}
}

